import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { FormGroup, FormControl, Button } from 'react-bootstrap';
import logo from '../assets/moon.png';
import DUMMY_WALLET from '../../../config';

class App extends Component {
    state = { walletInfo: DUMMY_WALLET, is_connected: false, publicKey: ''};

    componentDidMount(){
        fetch(`${document.location.origin}/api/current-wallet`)
            .then(response => response.json())
            .then(json => {
                if( json.address !== DUMMY_WALLET.address ){
                    this.setState({ walletInfo:json, is_connected: true});
                }
            });
    }

    updatePubKey = event => {
        this.setState({ publicKey: event.target.value })
    }

    loadWallet = () => {
        const { publicKey } = this.state;
        console.log(publicKey, '\n\n\n\n');

        fetch(`${document.location.origin}/api/wallet-info`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ publicKey })
        })
            .then(response => response.json())
            .then(json => {this.setState({ walletInfo: json, is_connected: true });

        });
    }

    createWallet = () => {
        fetch(`${document.location.origin}/api/create-wallet`)
            .then(response => response.json())
            .then(json => {this.setState({ walletInfo: json, is_connected: true });
            console.log(json);
        });
    }


    connectButton(){
        if(!this.state.is_connected){
            return(
                <Button
                    bsStyle='danger'
                    label='Connect to Wallet'
                    onClick={ this.loadWallet }
                >
                    Connect to Wallet
                </Button>
            )
        }
    }
    // connectForm(){
    //     if(!this.state.is_connected){
    //         return(
    //             <FormGroup>
    //             <FormControl
    //                 input='text'
    //                 placeholder='Enter Your Wallet Public Key'
    //                 value={this.state.publicKey}
    //                 onChange={this.updatePubKey}
    //             />
    //             </FormGroup>
    //         )
    //     }
    // }

    // createButton(){
    //     if(!this.state.is_connected){
    //         return(
    //             <Button
    //                 bsStyle='danger'
    //                 label='Create New Wallet'
    //                 onClick={ this.createWallet }
    //             >
    //                 Create New Wallet
    //             </Button>
    //         )
    //     }
    // }

    addressDisplay(){
        if(this.state.walletInfo !== DUMMY_WALLET){
            return(<div>Address: {this.state.walletInfo.address}</div>)
        }
    }
    balanceDisplay(){
        if(this.state.walletInfo !== DUMMY_WALLET){
            return(<div>Balance: {this.state.walletInfo.balance}</div>)
        }
    }

    render() {
        const { address, balance } = this.state.walletInfo;


        return(
            <div className='App'>
                <img className = 'moon' src={logo} height={190} width={184}></img>
                <br/>
                <div>
                    Welcome to MoonCoin
                </div>
                <br/>
                <div><Link to='/blocks'>Blocks</Link></div>
                <div><Link to='/conduct-transaction'>Conduct Transaction</Link></div>
                <div><Link to='/transaction-pool'>Transaction Pool</Link></div>
                <br/>
                <br/>
                <div className='WalletInfo'>
                    {
                        this.addressDisplay()
                    }
                    {
                        this.balanceDisplay()
                    }
                    {
                        this.connectForm()
                    }
                    
                    {
                        this.connectButton()
                    }
                    
                    <br/>
                    <br/>
                    {
                        this.createButton()
                    }
                    
                </div>
            </div>
        );
    }
}

export default App;